import CardComponent from "./components/CardComponent";
//import { Context } from "./context/context";

type Posting = {
  id:number;
  title: string;
  description: string;
  responsibilities: string[];
  ideal_candidate: {
    age: string;
    gender: string;
    traits: string[];
  };
  when_where: string;
  about: {
    posted_on: Date;
    deadline: Date;
    location: string;
    start_date: string;
    end_date: string;
    categories: string[];
    required_skills: string[];
  };
  company: string;
  image: string;
};

type JobData = {
  job_postings: Posting[];
};
//main page
export default async function Home() {
  const res = await fetch(
    "https://drive.google.com/uc?export=download&id=1QAObCWl5f0Ytc3bUHRREdNVBd4P-dO07"
  );

  if (!res.ok) {
    throw new Error("Failed to fetch job postings");
  }

  let data;
  try {
    data = await res.json();
  } catch (e) {
    throw new Error("Response is not valid JSON");
  }

  return (
    <div>
  
        {data.job_postings.map((job: Posting) => (
          <CardComponent key={job.id} job={job} /> // Ensure key is job.id, not index
        ))}
      

    </div>
  );
}

